.slider {
  position: relative;
  width: 100%;
}

.slider__toggle {
  transition-duration: 295ms;
  transition-property: background,  transform;
  transition-timing-function: ease;
  position: absolute;
  top: 0;
  right: 0;
  font-size: 12px;
  cursor: pointer;
  z-index: 10;
}
.slider__toggle .toggle-close {
  display: none;
}
.slider__toggle.thumbnails-open .toggle-open {
  display: none;
}
.slider__toggle.thumbnails-open .toggle-close {
  display: block;
}
@media print, screen and (min-width: 40em) {
  .slider__toggle {
    right: 0;
  }
}
@media print, screen and (min-width: 64em) {
  .slider__toggle {
    top: 0;
  }
}

.slider__toggle--fixed {
  position: fixed;
  top: 40px;
  right: 40px;
}
