}

.slider__inner {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  outline: none;
}

.slide {
  display: flex;
  width: calc(100% + 80px);
  height: calc(100vh - 80px);
  padding: 40px;
  align-items: center;
}
.slide a {
  display: block;
  width: 100%;
}
.slide img {
  width: 100%;
  height: calc(100vh - 160px);
  object-fit: contain;
}

.slide.is-selected {
  position: absolute;
  top: 0;
}

.slide--left img {
  object-position: left center;
}
.slide--left .slide__label {
  text-align: left;
}

.slide--right img {
  object-position: right center;
}
.slide--right .slide__label {
  text-align: right;
}

.slide--center img {
  margin: auto;
}
.slide--center .slide__label {
  text-align: center;
}

.slide__label {
  position: absolute;
  width: 100%;
  left: 0;
  bottom: 0;
  padding: 0 80px;
  font-size: 12px;
}
